import Kefu from '@/components/Kefu.vue';
export default {
	components:{Kefu},
	data() {
		return {
			user_head:'../../static/image/head.jpg',
			user_info: null
		};
	},
	onLoad() {
		this.$http.post('/v5/account/get_by_token',{}).then(res=>{
			if(res.code==1){
				this.user_info=res.data;
			}else{
				this.user_info=res.data;
			}
		})
	},
	methods:{
		openKefu(){
			this.$refs.kefu.show();
		}
	}
}
